import { Button } from '@mui/material';
import { Stack } from '@mui/system';
import { sendDataRelayStart, sendDataStrategyStart } from '../../common/sendData';
import { useTeamState } from '../hooks/user-hooks';

export function ChooserItem(props: {setStarted: React.Dispatch<string>, type: 'strategias'|'relay' }) {
  const teamState = useTeamState();
  return (
    <Stack sx={{
      display: 'flex',
      width: 'calc(50% - 50px)',
      padding: "40px",
      backgroundColor: "#fff",
    }}>
      <Stack sx={{
        fontWeight: 'bold',
        fontSize: 24,
        textAlign: 'center',
      }}>
        {props.type=='relay' ? "Vltfeladatok" : "Stratgis jtk"}
      </Stack>
      {props.type == 'relay' &&
        <Stack sx={{
          fontSize: 14,
          height: "100%",
          display: 'flex',
          alignItems: "center",
          flexDirection: 'row',
        }}>
          <Stack>
            A vltfeladatok rvid szveges feladatok, melyekre a vlasz egy egsz szm (legalbb 0 s legfeljebb 9999). Minden vltfeladatra maximum 3 tippet adhattok le, egy-egy rossz tipp utn az adott feladatra megszerezhet pontszm 1-gyel cskken. Helyes tipp vagy hrom rossz tipp utn megkapjtok a kvetkez feladatot. A vltfeladatokra sszesen 60 perc ll rendelkezsre, s sszesen 40 pont szerezhet velk hibtlan teljests esetn. (Az egyes feladatokra kaphat pontszm 3-tl 6-ig terjed.)
          </Stack>
        </Stack>
      }        
      {props.type == 'strategias' &&
        <Stack sx={{
          fontSize: 14,
          height: "100%",
          paddingBottom: "10px",
          "& p": {
            marginBlockEnd: 0,
          },
        }}>
          <p>Ebben a feladatban egy ktszemlyes stratgis jtkot jtszhattok, ahol az egyik jtkos Ti lesztek, a msik jtkos pedig a gp. Gyzztek le a gpet <b>ktszer egyms</b> utn ebben a jtkban! A kezd helyzet ismeretben Ti dnthetitek el, hogy a kezd vagy a msodik jtkos brbe szeretntek bjni.</p>         
          <p>A sikeres teljestsrt az albbiak szerint kaptok pontot:
            <ul>
              <li>Ha egyszer sem vesztetek, akkor 12 pontot kaptok.</li>
              <li>1, 2, 3, 4, illetve 5 veresg esetn a kapott pontszm 9-re, 6-ra, 4-re, 3-ra, illetve 2-re cskken.</li>
              <li>Ennl tbb veresg esetn is 2 pontot kaptok, ha sikerl teljestenetek a jtkot.</li>
              <li>Ha 30 perc alatt nem gyzitek le ktszer egyms utn a gpet, akkor 0 pontot kaptok.</li>
            </ul>
          </p>
          <p>
            Ha kszen lltok, kattintsatok a lenti gombra s onnantl kezdve sszesen 30 perc ll rendelkezsetekre.
          </p>
        </Stack>
      }  
      <Button sx={{
        width: '100%',
        height: '75px',
        fontSize: '26px',
        alignSelf: 'center',
        textTransform: 'none',
      }} variant='contained' color='primary' onClick={()=>{
        props.setStarted(props.type)
        if(props.type === "relay"){
          sendDataRelayStart(teamState)
        }else{
          sendDataStrategyStart(teamState)
        }

      }}>
        Kezdjk
      </Button>
    </Stack>
  )
}