import { MyGame } from './game';
import { MyBoard } from './board';
import { strategyWrapper } from './strategy';
import { ClientFactory } from '../../common/client_factory';


let descriptionC = <p className="text-justify">
Kezdetben van 10 rme az asztalon, melyek rtkei 1 s 4 kz es egszek lehetnek. A kt jtkos felvltva lp. A soron lv jtkos kivlaszt egy K rtket, amire igaz, hogy van az asztalon K rtk rme, s az sszes K rtk rmt tvltoztatja valamilyen kisebb L rtkre (mindet ugyanarra az L rtkre, ahol az L rtk 1 s K-1 kztti). Az nyer, akinek a lpse utn minden rme azonos rtk lesz. A kezdlls ismeretben Ti dnthetitek el, hogy a kezd vagy a msodik jtkos brbe szeretntek-e bjni.

(K: Melyik tpus rmt vltoztatjtok meg?; L: Mi legyen az j rme)
</p>

let descriptionDE = <p className="text-justify">
Kezdetben van 10 rme az asztalon, melyek rtkei 1 s 5 kz es egszek lehetnek. A kt jtkos felvltva lp. A soron lv jtkos kivlaszt egy K rtket, amire igaz, hogy van az asztalon K rtk rme, s az sszes K rtk rmt tvltoztatja valamilyen kisebb L rtkre (mindet ugyanarra az L rtkre, ahol az L rtk 1 s K-1 kztti). Az nyer, akinek a lpse utn minden rme azonos rtk lesz. A kezdlls ismeretben Ti dnthetitek el, hogy a kezd vagy a msodik jtkos brbe szeretntek-e bjni.

(K: Melyik tpus rmt vltoztatjtok meg?; L: Mi legyen az j rme)
</p>

export const { Client: Client_C, ClientWithBot: ClientWithBot_C, OnlineClient: OnlineClient_C } = ClientFactory(MyGame, MyBoard, strategyWrapper("C"), descriptionC);
export const { Client: Client_D, ClientWithBot: ClientWithBot_D, OnlineClient: OnlineClient_D } = ClientFactory(MyGame, MyBoard, strategyWrapper("D"), descriptionDE);
export const { Client: Client_E, ClientWithBot: ClientWithBot_E, OnlineClient: OnlineClient_E } = ClientFactory(MyGame, MyBoard, strategyWrapper("E"), descriptionDE);
