import { Button, Dialog, Stack } from "@mui/material";
import { useEffect, useState } from "react";
import { Finished } from "../client/components/teamsates/Finished";
import { Countdown } from "../components/Countdown";

export function boardWrapper(board: any, description: any) { // TODO: solve types with BoardProps<MyGameState>
  return ({ G, ctx, moves, log }: any) => {
    const [secondsRemaining, setSecondsRemaining] = useState(G.milisecondsRemaining as number | null); // asked from the server

    useEffect(() => {
      setSecondsRemaining(G.milisecondsRemaining);
    }, [G.milisecondsRemaining]);
    return (
      <>
        <Dialog open={
          !secondsRemaining || secondsRemaining < - 10000 || ctx.gameover === true
        } onClose={() => { window.location.reload(); }}>
          <Finished score={G.points}/>
        </Dialog>
        <Stack sx={{
          padding: '20px',
          backgroundColor: '#FFF',
          borderRadius: "25px",
          display: 'flex',
        }}>
          <Stack sx={{
            flexDirection: 'row',
            fontSize: '20px',
            fontWeight: 'bold',
            width: '100%',
          }}>
            <Stack sx={{
              width: '225px',
            }}>
              Stratgis jtk
            </Stack>
            <Stack sx={{ width: '100%' }} />
            <Stack sx={{
              flexDirection: 'row',
              width: '262px'
            }}>
              <b style={{ marginRight: '5px', width: '135px' }}>Htralev id:</b>
              <Countdown
                secondsRemaining={secondsRemaining ? secondsRemaining : null}
                setSecondsRemaining={setSecondsRemaining}
                getServerTimer={moves.getTime} />
            </Stack>
          </Stack>
          <Stack sx={{
            marginTop: '10px',
            fontSize: '24px',
            fontWeight: 'bold',
          }}>
            10 rme
          </Stack>
          <Stack sx={{
            fontSize: '14px',
          }}>
            {description} <br />
            Az "j prbajtk" gombra kattintva prbajtk indul, ami a pontozsba nem szmt bele. Btran krjetek prbajtkot, hiszen ezzel tudjtok tesztelni, hogy jl rtitek-e a jtk mkdst. Az "j les jtk" gombra kattintva indul a valdi jtk, ami mr pontrt megy.
          </Stack>
          <Stack sx={{
            width: '100%',
            flexDirection: 'row',
          }}>
            <Stack sx={{
              width: '40%',
              flexDirection: 'column',
              padding: '15px',
            }}>
              { ctx.phase === 'startNewGame' &&
              <Stack sx={{
                width: '100%',
                flexDirection: 'row',
                marginBottom: '20px',
              }}>
                <Button sx={{
                  width: '45%',
                  height: '60px',
                  fontSize: '18px',
                  alignSelf: 'center',
                  textTransform: 'none',
                  borderRadius: '10px',
                }} variant='contained' color='primary' onClick={() => {
                  moves.chooseNewGameType("test");
                }}>
                  j prbajtk kezdse
                </Button>
                <Stack sx={{ width: '10%' }} />
                <Button sx={{
                  width: '45%',
                  height: '60px',
                  fontSize: '18px',
                  alignSelf: 'center',
                  textTransform: 'none',
                  borderRadius: '10px',
                }} variant='contained' color='primary' onClick={() => {
                  moves.chooseNewGameType("live")
                }}>
                  j jtk kezdse
                </Button>
              </Stack>
              }
              {ctx.phase === 'chooseRole' &&
                <Stack sx={{
                width: '100%',
                flexDirection: 'row',
                marginBottom: '20px',
              }}>
                <Button sx={{
                  width: '45%',
                  height: '60px',
                  fontSize: '18px',
                  alignSelf: 'center',
                  textTransform: 'none',
                  borderRadius: '10px',
                }} variant='contained' color='primary' onClick={() => {
                  moves.chooseRole(0);
                }}>
                  Kezd leszek
                </Button>
                <Stack sx={{ width: '10%' }} />
                <Button sx={{
                  width: '45%',
                  height: '60px',
                  fontSize: '18px',
                  alignSelf: 'center',
                  textTransform: 'none',
                  borderRadius: '10px',
                }} variant='contained' color='primary' onClick={() => {
                  moves.chooseRole(1);
                }}>
                  Msodik leszek
                </Button>
              </Stack>}
              <Stack sx={{
                width: '100%',
                display: 'block',
                borderRadius: '20px',
                backgroundColor: '#93F272',
                borderColor: '#2DAD3A',
                borderWidth: '2px',
                textAlign: 'center',
                padding: '15px',
                fontSize: '14px',
                borderStyle: 'solid',
              }}>
                {ctx.phase === 'startNewGame' && G.winner === null && <p> Kezdj j jtkot! </p>}
                {ctx.phase === 'chooseRole' && <p> Vlaszd ki, hogy els vagy msodik jtkos akarsz-e lenni. </p>}
                {ctx.phase === 'play' && ctx.currentPlayer === "0" && <p> Most Te jssz! </p>}
                {ctx.phase === 'play' && ctx.currentPlayer === "1" && <p> Vrakozs a szerverre... </p>}
                {ctx.phase === 'startNewGame' && G.winner === "0" && <p> Gratullok, nyertetek! Verjtek meg mg egyszer a gpet!</p>}
                {ctx.phase === 'startNewGame' && G.winner === "1" && <p> Sajnos a gp nyert. </p>}
              </Stack>
            </Stack>
            <Stack sx={{
              width: '60%',
              flexDirection: 'row',
              padding: '15px',
            }}>
              {board({ G, ctx, moves })}
            </Stack>
          </Stack>
        </Stack>
      </>
    );
  };
};